<template lang="pug">
  .row.home
    Modal(:visible="modalVisible")
      .contact-card
        .info
          .message Shoot me an email!
          input(value="mccallalexander96@gmail.com")
        .message ...or contact me here!
        .social-links
          a(href="https://www.instagram.com/mccallalexander/", target="_blank")
            i.fa.fa-instagram.fa-2x
          a(href="https://github.com/mccallofthewild", target="_blank")
            i.fa.fa-github.fa-2x
          a(href="https://twitter.com/mccallthetall", target="_blank")
            i.fa.fa-twitter.fa-2x
    .curve
      sine(v-for="(a,i) in Array.from({length:30})", v-if="!isMobile")
    .col-xs-12
      .row
        .col-xs-12
          header
            h1 McCall Alexander
            h2 Web Development & Design
            address Boise, Idaho
            button(@click="showModal")
              | Let's Code
            nav(v-if="false")
              ul
                li
                  a(href="https://www.instagram.com/mccallalexander/", target="_blank")
                    i.fa.fa-instagram.fa-2x
                li
                  a(href="https://github.com/mccallofthewild", target="_blank")
                    i.fa.fa-github.fa-2x
                li
                  a(href="https://twitter.com/mccallthetall", target="_blank")
                    i.fa.fa-twitter.fa-2x
          ShootingStar(v-for="(a,i) in Array.from({length:30})")
          .sun-container
            sun
          .mountains
            mountain.left
            mountain.right
        .col-xs-12
          .projects.row
            .scroll-prompt
              i.material-icons arrow_downward
              p PROJECTS
            // project(v-for="(project, index) in Array.from({length:20})", :index="index", :project="project")
            project(v-for="(project, index) in projects", :key="index", :index="index", :project="project")
</template>

<script>
  import Project from './Project'
  import Modal from './Modal'
  import projects from './projects'
  import Sine from './Sine'
  import Mountain from './Mountain'
  import Sun from './Sun'
  import ShootingStar from './ShootingStar'
  import isMobile from 'ismobilejs'
  export default {
    name: 'hello',
    data () {
      return {
        msg: 'Welcome to Your Vue.js App',
        scrollOrigin:0,
        modalVisible:false
      }
    },
    computed:{
      projects(){
        return projects;
        return projects.sort((a,b)=>{
          return a.weight - b.weight
        }, {weight:0})
      },
      isMobile(){
        return isMobile.any
      },
    },
    components:{
      Project,
      Sine,
      Mountain,
      Sun,
      ShootingStar,
      Modal,
    },
    methods:{
      handleScroll(){
        let winHeight = window.innerHeight
        let centerScrollPos = window.scrollY - winHeight/2
        this.scrollOrigin = centerScrollPos
      },
      showModal(){
        this.modalVisible = true;
      }
    },
    mounted(){
      console.log(projects)
      let vm = this;
      let ecount = 0;
      window.addEventListener('scroll', this.handleScroll);
      this.handleScroll()
    },
    beforeDestroy () {
      let a = 0
      window.removeEventListener('scroll');
    }
  }
</script>

