<template lang="pug">
  .modal-container(v-if="visible")
    .modal-bg(@click="hide")
    .modal
      button.close(@click="hide")
        i.fa.fa-close
      slot
</template>

<script>

export default {
  props:{
    visible:{
      type:Boolean,
      default(){
        return false;
      },
    }
  },
  methods:{
    hide(){
      this.$parent.modalVisible = false;
    }
  }
}
</script>

<style lang="scss">
  @import "../assets/css/global";

  .modal-container{
    .modal-bg{
      position:fixed;
      top:0;
      bottom:0;
      left:0;
      right:0;
      height:100%;
      width:100%;
      background:rgba(10, 26, 45, 0.7);

    }
    display: flex;
    justify-content: center;
    align-items: center;
    position:fixed;
    top:0;
    bottom:0;
    left:0;
    right:0;
    height:100%;
    width:100%;
    z-index:1000;
    overflow: hidden;
    .modal{
      position:relative;
      cursor:default;
      width:50%;
      height:50%;
      @include mobile{
        width:75%;
        height:80%;
      }
      background:white;
      box-shadow: 0px 0px 15px rgba(10, 26, 45, 0.9);
      .close{
        position:absolute;
        color:rgba(black, 0.8);
        top:5px;
        right:10px;
        cursor:pointer;
      }
    }
  }
</style>


